  <div class="replayer" :style="mainStyle">

    <a v-if="pageType==='worldCup'" class="goback-nav" id="nav" :href="'mainpage.html?ADTAG='+isqd+'&worldCup'">
      <img src="../../assets/images/back.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
      <span>低调看直播</span>
    </a>
    <a v-else class="goback-nav" id="nav" :href="'mainpage.html?ADTAG='+isqd+'&review'">
      <img src="../../assets/images/back.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
      <span>低调看直播</span>
    </a>
    <contact :Banner="bannerArr.contact"></contact>

    <!--比分-->
    <timeScore :IsBlack="false"></timeScore>
    <div :class="{'box-list':true,top:istop}" id="boxList">
      <div :class="{title:true,top:istop}">
        <div>
          <a :class="navType?'active':''" href="javascript:void(0)" @click="changeNav(true)">集锦</a>
          <a :class="navType?'':'active'" href="javascript:void(0)" @click="changeNav(false)">回看</a>
        </div>
      </div>

      <div v-show="loading" class="loading">
        <img src="../../assets/images/loading.gif" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
        <span>正在加载中...</span>
      </div>
      <!--集锦-->
      <div v-if="navType" class="collection-box">
        <ul>
          <li v-for="(item,index) in collectionList" :key="index">
            <list-ban :ListBan="bannerArr.listBan" v-if="item.bannerType" ></list-ban>
            <div class="content">
              <a v-if="item.playType==2" :href="item.url" target="_blank"></a>
              <a
                v-if="item.playType==1||item.redirect==false"
                :href='"player.html?ADTAG="+isqd+"&isChannel="+channelType+"&playType="+item.playType+"&matchId="+matchId+"&src="+isencode(item.url)+"&title="+item.leagueName' target="_self">
              </a>
              <div class="text">
                <div class="li-title"
                     style="word-break: break-all;overflow: hidden;  -webkit-line-clamp: 2 ;-webkit-box-orient: vertical;display: -webkit-box ;text-overflow: ellipsis;">
                  {{item.title}}
                </div>
                <div class="li-info">
                  <span>播放：{{item.read}}</span>
                  <!--<span>时长：31:10</span>-->
                </div>
              </div>
              <div class="poster">
                <img :src="item.image" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
              </div>
            </div>
          </li>
        </ul>

      </div>
      <!--回看-->
      <div v-else class="replayer-box">
        <ul>
          <li v-for="(item,index) in reviewList" :key="index">
            <list-ban :ListBan="bannerArr.listBan" v-if="item.bannerType" ></list-ban>
            <div class="content">
              <a v-if="item.playType==2" :href="item.url" target="_blank"></a>
              <a
                v-if="item.playType==1"
                :href='"player.html?ADTAG="+isqd+"&isChannel="+channelType+"&playType="+item.playType+"&matchId="+matchId+"&src="+isencode(item.url)+"&title="+item.leagueName' target="_self">
              </a>
              <div class="text">
                <div class="li-title"
                     style="overflow: hidden; -webkit-line-clamp: 2 ;-webkit-box-orient: vertical;display: -webkit-box ;text-overflow: ellipsis;">
                  {{item.title}}
                </div>
                <div class="li-info">
                  <span>播放：{{item.playTimes}}</span>
                  <span>时长：{{getTime(item.time)}}</span>
                </div>
              </div>
              <div class="poster">
                <img src="../../assets/images/review_play.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
                <span>回看 / 集锦</span>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div v-if="nodata" class="nodata">
        <img src="../../assets/images/nodata.png" alt="低调看直播_世界杯直播_世界杯小组赛_世界杯淘汰赛_jrs直播_直播体育_西甲直播_NBA直播吧_英超直播_高清免费_007看球_足球直播_NBA直播_英超直播_007kq_007直播">
      </div>
    </div>
    <div style="width: 100%;height: 20px;"></div>
    <transition name="showline">
    <tips :Tips="tipsText" v-if="showtips"></tips>
    </transition>
    <downlink :Banner="bannerArr.downlink"></downlink>
  </div>
</template>

<style lang="less" >
  @import "../../common/css/replayer.less";
</style>
<script type='text/ecmascript-6'>
  import * as utils from '../../common/js/utils'
  import tips from '../components/tips/tips.vue'
  import Vue from 'vue'
  import downlink from '../components/downlink.vue'
  import contact from '../components/contact.vue'
  import timeScore from '../components/timeScore.vue'
  import listBan from '../components/listBan.vue'
  export default{
    name: 'replayer',
    components: {tips, downlink, contact,timeScore,listBan},
    data() {
      return {
        playType: parseInt(utils.getPara('playType')),
        matchId: utils.getPara('matchId'),
        pageType: utils.getPara('type'),
        channelType: 2, // 2普通回看，3世界杯回看
        mainStyle:{
          paddingBottom: '1.13rem'
        },
        lineId:'',
        firstType:'',
        tipsText:'',
        showtips:false,
        matchType:1000,

        showdownlink:false,
        mainStyle:{
          paddingBottom: '1.13rem'
        },
        isqd:'',

        navType:true, // true 为集锦 false为回看
        loading:true,
        isreplayer:false,
        collectionList:[],
        reviewList:[],

        nodata:false,
        istop:false,
        realH:0,

        isFisrt:true,
        bannerArr:{
          downlink:{iosShow:false,androidShow:false},
          contact:{iosShow:false,androidShow:false},
          appPop:{iosShow:false,androidShow:false},
          listBan:{iosShow:false,androidShow:false},
        },
      }
    },
    created(){
      this.isqd = utils.getPara('ADTAG')?utils.getPara('ADTAG'):'';
      document.getElementById('loading').style.display = 'none';
      if(this.pageType === 'worldCup') {
        this.channelType = 3
      }
      if(sessionStorage.getItem('downlink')){
        this.showdownlink = true;
      }

      this.scrollload()

      this.getCollectionList();
      this.getBannerData();
    },
    mounted() {
      let clientW = document.documentElement.clientWidth || document.body.clientWidth;
      let htmlFontSize = 100 * (clientW / 750)
      let domRem = 0.88 + 1.67 + 1.54
      let realH = (htmlFontSize*domRem).toFixed(2)
      this.realH = realH
    },
    methods: {
      getBannerData(){
        let toData = {
          url:'ggw/mobile/getAll',
          callback:(res)=>{
            /*console.log(res.data)*/
            if(res.error_code==1){
              let list = res.data
              //  type广告位置类型 0：底层广告 1：列表广告 2：悬浮窗广告 3：文字广告
              list.map((item,index)=>{
                switch (item.type){
                  case 0:
                    this.bannerArr.downlink = item
                    break;
                  case 1:
                    this.bannerArr.listBan = item
                    break;
                  case 2:
                    this.bannerArr.appPop = item
                  case 3:
                    this.bannerArr.contact = item
                    break;
                }
              })
            }
          }
        }
        utils.cpost(this,toData)
      },
      getCollectionList(){
        this.loading = true
        this.nodata = false
        let toData = {
          url:'right/game_video_list',
          postData:{
            gameId:utils.getPara('matchId'),
          },
          callback: (res)=>{
            /*console.log(res)*/
            this.loading = false
            if(res.error_code==1){
              this.collectionList = res.data
              if(this.collectionList.length==0){
                this.nodata = true
              }else{
                this.collectionList.map((item,index)=>{
                  item.bannerType = false
                  if(index%7==0&&index!=0){
                    item.bannerType = true
                  }
                })
              }
            }else{
              this.nodata = true
            }
          }
        }
        utils.cpost(this,toData)
      },
      getReviewList(){
        this.loading = true
        this.nodata = false
        let toData = {
          url:'right/game_progress_info',
          postData:{
            gameId:utils.getPara('matchId'),
          },
          callback: (res)=>{
            this.loading = false
            /*console.log(res);*/
            if(res.error_code==1&&res.data.backLooks){
              this.reviewList = res.data.backLooks
              if(this.reviewList.length==0){
                this.nodata = true
              }else{
                this.reviewList.map((item,index)=>{
                  item.bannerType = false
                  if(index%7==0&&index!=0){
                    item.bannerType = true
                  }
                })
              }
            }else {
              this.nodata = true
            }
          }
        }
        utils.cpost(this,toData)
      },
      changeNav(type){
        this.nodata = false
        this.navType = type
        if(type){
          this.nodata = (this.collectionList.length==0?true:false)
        }else{
          this.nodata = (this.reviewList.length==0?true:false)
        }
        if(this.isFisrt){
          this.isFisrt = false
          this.getReviewList();
        }
        if(this.istop){
          document.documentElement.scrollTop = document.body.scrollTop = 200;
        }
      },
      scrollload(){
        window.onscroll = ()=>{
          let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;//滚动条y轴上的距离
          let clientH = document.documentElement.clientHeight || document.body.clientHeight;//可视区域的高度
          let windowH = document.documentElement.scrollHeight || document.body.scrollHeight;//可视化的高度与溢出的距离（总高度）
          /*console.log(scrollTop)*/
          if(scrollTop>this.realH){
            this.istop = true
          }else{
            this.istop = false
          }

        }
      },
      closedownlink(type){
        this.showdownlink = type;
        if(type==false){
          this.mainStyle.paddingBottom = '0'
          sessionStorage.setItem('downlink',false)
        }
      },
      isencode(src){
        if(src.indexOf('rtmp')>-1&&src.indexOf('.html')<0){
          return src
        }else{
          return encodeURIComponent(src)
        }
      },

      getTime(time){
        let m,s,str
        if(time){
          m = time/60
          s = time%60
          str = parseInt(m) + ':' + (s<10?s+'0':s);
        }
        return str
      },
    },
  }



</script>



